import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
// import {Link} from 'react-router-dom';
import BackEndLayout from "../../../layouts/BackEndLayout";
import { getAllFaqs, deleteAFaq } from "../../../../actions/faqActions";
import MaterialTable from "material-table";
import { Link } from "@material-ui/core";
import BackEndFaqAdd from "./BackEndFaqAdd";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMore from "@mui/icons-material/ExpandMore";
import { red } from "@mui/material/colors";

const BackEndFaqCrud = (props) => {
  const faqs = useSelector((state) => state.faq.faqs);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getAllFaqs());
  }, []);
  //private function
  function deleteThisFaq(id) {
    if (window.confirm("Are you Sure, you want to delete this ?") == true) {
      dispatch(deleteAFaq(id));
    }
  }

  const columns = [
    { title: "TITLE", field: "title" },
    { title: "BODY", field: "body" },
    {
      title: "Action",
      color: red,
      render: (rowData) => (
        <React.Fragment>
          {/* <Link
            to={`/admin/faq/edit/${rowData._id}`}
            // onClick={() => setActiveUser(rowData, rowData._id)}
            className="btn btn-sm btn-success text-white"
            type="button"
          >
            <i className="fa fa-edit"></i> Edit
          </Link> */}
          <button
            onClick={() => deleteThisFaq(rowData._id)}
            className="ml-2 btn btn-sm btn-danger"
          >
            <i className="fa fa-trash"></i> Delete
          </button>
        </React.Fragment>
      ),
    },
  ];
  const options = {
    search: true,
    paging: true,
  };
  const tableTitle = "FAQs' List";

  return (
    <BackEndLayout>
      <div className="content-wrapper">
        <div className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                {/* <h1 className="m-0 text-dark">FAQs</h1> */}
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item">
                    <Link to="/">Home</Link>
                  </li>
                  <li className="breadcrumb-item active">FAQs</li>
                </ol>
              </div>
            </div>
          </div>
        </div>
        <section className="content">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <Accordion>
                  <AccordionSummary
                    expandIcon={<ExpandMore />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                  >
                    <Typography>
                      Click here to{" "}
                      <span style={{ color: "red", fontStyle: "italic" }}>
                        Add FAQ <i className="fa fa-plus"></i>
                      </span>
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <BackEndFaqAdd />
                  </AccordionDetails>
                </Accordion>
                <div className="card">
                  <div className="card-body">
                    {faqs && faqs.length > 0 ? (
                      <MaterialTable
                        title={tableTitle}
                        data={faqs}
                        columns={columns}
                        options={options}
                      />
                    ) : (
                      "No FAQ found"
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </BackEndLayout>
  );
};

export default BackEndFaqCrud;
