import { React, Fragment, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import FrontEndHeader from "./FrontEndHeader";
import FrontEndFooter from "./FrontEndFooter";

import { channelList } from "../assets/ChannelList";
import { ToastContainer } from "react-toastify";

function FrontEndLayout(props) {
  // const auth = useSelector((state) => state.auth);
  const [channels, setChannels] = useState(channelList.items.slice(0));
  const filterHandler = (val) => {
    const newChannels = channelList.items.filter((item) => {
      return item.raw.includes(val);
    });
    setChannels(newChannels);
  };
  return (
    <Fragment>
      <FrontEndHeader filterHandler={filterHandler} />
      {/* {auth.isAuthenticated && <SideBar auth={auth}/>} */}
      <ToastContainer />
      {props.children}
      <FrontEndFooter />
    </Fragment>
  );
}

export default FrontEndLayout;
