import React, { Component } from "react";
import { connect } from "react-redux";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";
import { saveFaq } from "../../../../actions/faqActions";
// import { toast } from 'react-toastify'
import TextFieldGroup from "../../../../common/TextFieldGroup";
import TextAreaFieldGroup from "../../../../common/TextAreaFieldGroup"; 

class BackEndFaqAdd extends Component {

  constructor(props) {
    super(props);
    this.state = {
      title: "",
      body: "",
      errors: "",
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }
  onSubmit(e) { 
    e.preventDefault();
    const userData = {
      title: this.state.title,
      body: this.state.body,
    };
    this.props.saveFaq(userData);
    this.setState({
      ...this.state,
      title: "",
      body: "",
    });
     
    window.location.reload(true);
  }
  onChange(e) {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  render() {
    const { title, body } = this.state;
    return (
      <React.Fragment>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <form noValidate onSubmit={this.onSubmit}>
                  <div className="row">
                    <div className="col-12">
                      <TextFieldGroup 
                        placeholder="Enter title"
                        onChange={this.onChange}
                        value={title ? title : ""}
                        type="text"
                        name="title"
                      />
                    </div>
                    <div className="col-12">
                      <TextAreaFieldGroup
                        placeholder="Enter body"
                        onChange={this.onChange}
                        value={body ? body : ""}
                        type="text"
                        name="body"
                      />
                    </div>
                    <div className="col-4 mt-2">
                      <button
                        type="submit"
                        className="btn btn-info btn-sm text-white "
                      >
                        Add FAQ
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
BackEndFaqAdd.propTypes = {
  errors: PropTypes.string.isRequired,
};
function mapStateToProps(state) {
  return {
    errors: state.errors ? state.errors : {},
  };
}

export default connect(mapStateToProps, { saveFaq })(BackEndFaqAdd);
