import { GET_ERRORS } from "../actions/types";
import axios from "axios";
import { API_URL } from "../common/Constant";

const checkMac = (val) => (dispatch) => {
  let data = {
    mac: val,
  };
  const tovalidate = /^([0-9A-Fa-f]{2}[:-]){5}([0-9A-Fa-f]{2})$/;
  const pattern = new RegExp(tovalidate);
  console.log("pattern.test(val)", pattern.test(val));

  axios
    .post(API_URL + "/validation/validateMacAddress", data)
    .then((result) => {
      console.log("validateMacAddress result", result);
      if (result.data.success == true) {
        if (pattern.test(val)) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    })
    .catch((err) =>
      // dispatch({ type: GET_ERRORS, payload: err.response.data })
      dispatch({ type: GET_ERRORS, payload: err.response })
    );
};

export default checkMac;
