import { GET_ALL_COUNT_DASHBOARD } from "../actions/types";
// import isEmpty from "../../src/validations/is-empty";

const initialState = {
  getAllCount: [],
};
 
export default function (state = initialState, action) {
  switch (action.type) {
    case GET_ALL_COUNT_DASHBOARD:
      return {
        ...state,
        getAllCount: action.payload.data.data,
      };
    default:
      return state;
  }
}
