import React, { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, Link } from "react-router-dom";
import FrontEndLayout from "../../layouts/FrontEndLayout";
import { saveMacForm, deletePlaylist } from "../../../actions/macActions";
import TextFieldGroup from "../../../common/TextFieldGroup";
import { toast } from "react-toastify";
import isEmpty from "../../../validations/is-empty";
import checkLink from "../../../validations/checkLink";
import checkMac from "../../../validations/checkMac";

import "./FormPage.css";

const FormPage = (props) => {
  // const cloneBelow = useRef("");
  console.log("props", props);

  const dispatch = useDispatch();
  let history = useHistory();

  let [mac, setMac] = useState("");
  let [deleteMac, setDeleteMac] = useState("");
  let [deletePin, setDeletePin] = useState("");
  let [epgCountry, setSelectCountries] = useState("");
  // let [link, setLink] = useState("");
  let [file, setFile] = useState("");
  let [pin, setPin] = useState("");

  const SingleFileChange = (e) => {
    setFile(e.target.files[0]);
  };
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    if (name === "mac") {
      return setMac(value);
    }
    if (name === "deleteMac") {
      return setDeleteMac(value);
    }
    if (name === "epgCountry") {
      return setSelectCountries(value);
    }
    // if (name === "link") {
    //   return setLink(value);
    // }
    if (name === "pin") {
      return setPin(value);
    }
    if (name === "deletePin") {
      return setDeletePin(value);
    }
  };
  // let children = [<TextFieldGroup
  //   type="text"
  //   className="form-control"
  //   name="link"
  //   placeholder="Enter the link here."
  //   onChange={handleInputChange}
  // />];
  const clickAddFileBtn = (e) => {
    e.preventDefault();
    const temp = [...this.children];
    temp.push(1); // to increase counter
    // this.setState({children:temp});
  };

  const submitInsertForm = (e) => {
    e.preventDefault(); 
    if (!isEmpty(mac) && !isEmpty(epgCountry) && file && !isEmpty(file.name)) {
      // if (checkLink(link)) {
      if (checkMac(mac)) {
        const formData = new FormData();
        formData.append("mac", mac);
        formData.append("epgCountry", epgCountry);
        formData.append("file", file);
        formData.append("pin", pin);
        // let data = {
        //   mac: mac,
        //   epgCountry: epgCountry,
        //   file: file,
        //   pin: pin,
        // };
        dispatch(saveMacForm(formData, history));
      } else {
        toast.error("Wrong format! Please check the mac address format.");
      }
      // } else {
      //   toast.error("The links seems to be invalid.");
      // }
    } else {
      toast.error("Please enter all the field(s).");
    }
  };

  const submitDeleteForm = (e) => {
    e.preventDefault();
    if (!isEmpty(deleteMac)) {
      if (!isEmpty(deletePin)) {
        let data = {
          deleteMac: deleteMac,
          deletePin: deletePin,
        };
        dispatch(deletePlaylist(data, history));
      } else {
        toast.error("Please enter the required pin field.");
      }
    } else {
      toast.error("Please enter the required mac field.");
    }
  };

  return (
    <FrontEndLayout>
      <section className="content">
        <div className="container-fluid">
          <form encType="multipart/form-data" method="POST">
            <div className="row certainColor">
              <div className="col-6" style={{ borderRight: "1px solid grey" }}>
                <div className="row p-5">
                  <h5 className="pb-5">
                    Upload playlist Files or external playlist URLs with
                    auto-update.
                  </h5>
                  <p>
                    <i>
                      (<span className="text-danger">*</span> marked fields are
                      required)
                    </i>
                  </p>
                  <div className="col-12">
                    <div className="form-group">
                      <label htmlFor="mac">
                        MAC<span className="text-danger">*</span>
                      </label>
                      <TextFieldGroup
                        type="text"
                        className="form-control"
                        id="mac"
                        name="mac"
                        placeholder="00:aa:bb:cc:dd:11"
                        onChange={handleInputChange}
                      />
                    </div>
                  </div>
                  <div className="col-6">
                    <div className="form-group">
                      <label htmlFor="epg_country">
                        Select Epg Country<span className="text-danger">*</span>
                      </label>
                      <select
                        id="epgCountry"
                        className="epgCountry form-control"
                        name="epgCountry"
                        title="EPG Country"
                        onChange={handleInputChange}
                        defaultValue=""
                      >
                        <option value="" disabled>
                          EPG Country
                        </option>
                        <option value="USSR">ExUSSR</option>
                        <option value="Albania">Albania</option>
                        <option value="Argentina">Argentina</option>
                        <option value="Baltic">Baltic</option>
                        <option value="Benelux">Benelux</option>
                        <option value="Brazil">Brazil</option>
                        <option value="Bulgaria">Bulgaria</option>
                        <option value="Canada">Canada</option>
                        <option value="Chile">Chile</option>
                        <option value="CZ">CZ/SK</option>
                        <option value="Erotic">Erotic</option>
                        <option value="ExYu">ExYu</option>
                        <option value="France">France</option>
                        <option value="Germany">Germany</option>
                        <option value="Greece">Greece</option>
                        <option value="Hungary">Hungary</option>
                        <option value="India">India</option>
                        <option value="IPTV">IPTV</option>
                        <option value="Israel">Israel</option>
                        <option value="Italy">Italy</option>
                        <option value="Macedonia">Macedonia</option>
                        <option value="Mexico">Mexico</option>
                        <option value="Nordic">Nordic</option>
                        <option value="OSN">Mid East</option>
                        <option value="Peru">Peru</option>
                        <option value="Poland">Poland</option>
                        <option value="Portugal">Portugal</option>
                        <option value="Romania">Romania</option>
                        <option value="Spain">Spain</option>
                        <option value="Turkey">Turkey</option>
                        <option value="UK">UK</option>
                        <option value="USA">USA</option>
                        <option value="Various">Various</option>
                      </select>
                    </div>
                  </div>
                  <div className="col-6">
                    <label htmlFor="pin">
                      PIN<span className="text-danger">*</span>&nbsp;
                      <Link to="/recoverpin">(Recover Pin)</Link>
                    </label>
                    <TextFieldGroup
                      type="password"
                      className="form-control"
                      name="pin"
                      placeholder="Enter the pin here."
                      onChange={handleInputChange}
                    />
                  </div>
                  <div className="col-12">
                    <div className="row">
                      <div className="col-12">
                        <label htmlFor="file">
                          Upload File (.m3u)
                          <span className="text-danger">*</span>
                        </label>
                        <input
                          type="file"
                          name="file"
                          className="form-control"
                          onChange={(e) => SingleFileChange(e)}
                        />
                      </div>
                    </div>
                  </div>
                  {/* <div className="col-12">
                    <div className="row">
                      <div className="col-12">
                        <label htmlFor="link">
                          Link<span className="text-danger">*</span>
                        </label>
                        <TextFieldGroup
                          type="text"
                          className="form-control"
                          name="link"
                          placeholder="Enter the link here."
                          onChange={handleInputChange}
                        />
                        {/* {children.map((_, index) => (
                      
                    ))} 
                      </div>
                      <div className="col-3">
                    <label htmlFor="addLink" id="addLinkLabel">
                      Add More Link(s)
                      <button
                        type="button"
                        className="btn btn-success"
                        id="addLink"
                        onClick={() => clickAddFileBtn}
                      >
                        <i className="fa fa-plus"></i>
                      </button>
                    </label>
                  </div> 
                    </div>
                  </div> */}
                  <div className="col-2 my-2">
                    <div className="form-group">
                      <label htmlFor="saveBtn">Action</label>
                      <button
                        className="btn btn-sm btn-success"
                        onClick={submitInsertForm}
                      >
                        <i className="fa fa-save"></i>&nbsp;Save
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-6">
                <div className="row p-5 ">
                  <h5 className="pb-5">
                    Delete playlist(s) associated to the MAC Address.
                  </h5>
                  <p>
                    <i>
                      (<span className="text-danger">*</span> marked fields are
                      required)
                    </i>
                  </p>
                  <div className="col-12">
                    <div className="form-group">
                      <label htmlFor="deleteMac">
                        Enter the MAC Address to delete the playlist(s)
                        associated with it.{" "}
                        <span className="text-danger">*</span>
                      </label>
                      <TextFieldGroup
                        type="text"
                        className="form-control"
                        id="deleteMac"
                        name="deleteMac"
                        placeholder="00:aa:bb:cc:dd:11"
                        onChange={handleInputChange}
                      />
                    </div>
                  </div>
                  <div className="col-6">
                    <label htmlFor="deletePin">
                      PIN<span className="text-danger">*</span>&nbsp;
                      <Link to="/recoverpin">(Recover Pin)</Link>
                    </label>
                    <TextFieldGroup
                      type="password"
                      className="form-control"
                      name="deletePin"
                      placeholder="Enter the pin here."
                      onChange={handleInputChange}
                    />
                  </div>

                  <div className="col-12">
                    <div className="form-group">
                      <label htmlFor="saveBtn">Action</label> <br />
                      <button
                        className="btn btn-sm btn-danger"
                        onClick={submitDeleteForm}
                      >
                        <i className="fa fa-trash"></i>&nbsp;Delete
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </section>
      <hr />
      <section className="content">
        <div className="container-fluid"></div>
      </section>
    </FrontEndLayout>
  );
};

export default FormPage;
