// SUBMIT NEW PIN
export const submitNewPin = ({ email, pin }, history) => (dispatch) => {
  console.log("DATA email", email);
  console.log("DATA pin", pin);
  const data = {
    email: email,
    pin: pin,
  };
  axios
    .post(API_URL + "/activation/changeRecoveryPin", data)
    .then((result) => {
      console.log("asd123 123 123 12 3 result 1", result);
      dispatch(submitNewPinAction(result));
      if (result.data.success == false) {
        toast.error(result.data.message, { autoClose: 2300 });
      } else {
        history.push("/form");
        dispatch(resetRecoveryFormAction(result));
        toast.success(result.data.message, { autoClose: 2300 });
      }
    })
    .catch((err) =>
      // dispatch({ type: GET_ERRORS, payload: err.response.data })
      dispatch({ type: GET_ERRORS, payload: err })
    );
};

