import {
  SAVE_ACTIVATION,
  CREATE_PAYMENT_INTENT,
  SEND_RECOVERY_EMAIL,
  SUBMIT_RECOVERY_OTP,
  CHANGE_RECOVERY_PIN,
  RESET_RECOVERY_FORM,
} from "../actions/types";
// import isEmpty from "../../src/validations/is-empty";

const initialState = {
  mac: "",
  transactionId: "",
  approvePayment: true,
  recoveryEmailSent: false,
  OtpEnterDone: false,
  enterNewPinDone: false,
};

export default function activationReducer(state = initialState, action) {
  console.log("object: action123", action);
  let approve = true;
  switch (action.type) {
    case SAVE_ACTIVATION:
      if (action.payload.data.success == false) {
        approve = false;
      }
      return {
        ...state,
        approvePayment: approve,
        mac: action.payload.data.mac,
        transactionId: action.payload.data.transactionId,
      };

    case SEND_RECOVERY_EMAIL:
      if (action.payload.data.success == false) {
        return {
          ...state,
          recoveryEmailSent: false,
        };
      } else if (action.payload.data.success == true) {
        return {
          ...state,
          recoveryEmailSent: true,
        };
      }
    case SUBMIT_RECOVERY_OTP:
      if (action.payload.data.success == false) {
        return {
          ...state,
          OtpEnterDone: false,
        };
      } else if (action.payload.data.success == true) {
        return {
          ...state,
          OtpEnterDone: true,
        };
      }
    case CHANGE_RECOVERY_PIN:
      if (action.payload.data.success == false) {
        return {
          ...state,
          enterNewPinDone: false,
        };
      } else if (action.payload.data.success == true) {
        return {
          ...state,
          enterNewPinDone: true,
        };
      }
    case RESET_RECOVERY_FORM:
      if (action.payload.data.success == false) {
        return {
          ...state,
        };
      } else if (action.payload.data.success == true) {
        return {
          ...state,
          recoveryEmailSent: false,
          OtpEnterDone: false,
          enterNewPinDone: false,
        };
      }

    case CREATE_PAYMENT_INTENT:
      return {
        ...state,
        intent: action.payload.data,
      };
    default:
      return state;
  }
}
