import {
  SAVE_MAC_FORM,
  GET_ALL_MACS,
  DELETE_PLAYLIST,
  GET_PLAYLIST,
  GET_CURRENT_MAC_ADDRESS
} from "../actions/types";
// import isEmpty from "../../src/validations/is-empty";

const initialState = {
  mac: "",
  type: "",
};
 
export default function (state = initialState, action) {
  console.log("GRUMINDER ", action);
  switch (action.type) {
    case SAVE_MAC_FORM:
      return {
        ...state,
        mac: action.payload.data.mac,
        type: action.payload.data.type,
      };
    case GET_ALL_MACS:
      return {
        ...state,
        macs: action.payload.data.macs,
      };
    case DELETE_PLAYLIST:
      return {
        ...state,
      };
    case GET_PLAYLIST:
      return {
        ...state,
        channelList: action.payload.data.data,
      };
    case GET_CURRENT_MAC_ADDRESS:
      return {
        ...state,
        currentMacAddress: action.payload.data.data.en0,
      };
    default:
      return state;
  }
}
