import React, { useState, useEffect } from "react";
import FrontEndLayout from "../../layouts/FrontEndLayout";
import "./ActivationPage.css";
import { CardElement, useStripe, useElements } from "@stripe/react-stripe-js";
import { useSelector, useDispatch } from "react-redux";
import TextFieldGroup from "../../../common/TextFieldGroup";
import "./ActivationPage.css";
import { useHistory, Link } from "react-router-dom";

import { toast } from "react-toastify";
import isEmpty from "../../../validations/is-empty";
// import ReCAPTCHA from "react-google-recaptcha";
import checkMac from "../../../validations/checkMac";
import checkEmail from "../../../validations/checkEmail";
import {
  saveActivation,
  createPaymentIntent,
} from "../../../actions/activationActions";

const ActivationPage = (props) => {
  // const grecaptchaObject = window.grecaptcha;
  const dispatch = useDispatch();
  let [mac, setMac] = useState("");
  let [email, setEmail] = useState("");
  let [pin, setPin] = useState("");
  let [confirmPin, setConfirmPin] = useState("");
  // let [reCaptcha, setReCaptcha] = useState(""); 
  const [succeeded, setSucceeded] = useState(false);
  const [processing, setProcessing] = useState(false);
  const [error, setError] = useState(null); 
  const [disabled, setDisabled] = useState(true); 
  const stripe = useStripe();
  const elements = useElements();
  //fetching out intent form the reduced state.
  const { intent, paymentApprove } = useSelector((state) => state.activation);
  useEffect(() => {
    dispatch(createPaymentIntent());
  }, []);
  let history = useHistory();
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    if (name === "mac") {
      return setMac(value);
    }
    if (name === "email") {
      return setEmail(value);
    }
    if (name === "pin") {
      return setPin(value);
    }
    if (name === "confirmPin") {
      return setConfirmPin(value);
    }
  };
  function onCaptchaChange(value) {
    console.log("Captcha value:", value);
  }
  const cardStyle = {
    style: {
      base: {
        color: "#32325d",
        fontFamily: "Arial, sans-serif",
        fontSmoothing: "antialiased",
        fontSize: "16px",
        "::placeholder": {
          color: "#32325d",
        },
      },
      invalid: {
        fontFamily: "Arial, sans-serif",
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  };

  const handleChange = async (event) => {
    // Listen for changes in the CardElement
    // and display any errors as the customer types their card details
    setDisabled(event.empty);
    setError(event.error ? event.error.message : "");
  };

  const handleSubmit = async (ev) => {
    ev.preventDefault();

    if (!isEmpty(email)) {
      // if (checkEmail(email)) {
      if (!isEmpty(mac)) {
        // if (checkMac(mac)) {
        if (!isEmpty(pin)) {
          if (!isEmpty(confirmPin)) {
            if (pin == confirmPin) {
              let data = {
                mac: mac,
                pin: pin,
                email: email,
                transactionId: intent.transactionId,
              };
              //if everything is fine move onto payment
              setProcessing(true);

              dispatch(saveActivation(data, history));

              if (paymentApprove == true) {
                //payment only gets deducted when approval comes in.
                const payload = await stripe.confirmCardPayment(
                  intent.client_secret,
                  {
                    payment_method: {
                      card: elements.getElement(CardElement),
                    },
                  }
                );
                if (payload.error) {
                  setError(`Payment failed ${payload.error.message}`);
                  setProcessing(false);
                } else {
                  setError(null);
                  setProcessing(false);
                  setSucceeded(true);
                }
              }
            } else {
              toast.error("PIN and confirmation PIN do not match.");
            }
          } else {
            toast.error("Confirm PIN is required.");
          }
        } else {
          toast.error("PIN is required.");
        }
        // } else {
        //   toast.error("Mac Address is already taken. ");
        //   return;
        // }
      } else {
        toast.error("MAC address is required.");
      }
      // } else {
      //   toast.error("Email already taken. You might want to reactivate.");
      //   return;
      // }
    } else {
      toast.error("Email is required.");
    }
  };

  return (
    <FrontEndLayout>
      <div className="row p-3 certainColor">
        <div className="col-12 text-center my-3 ">
          <h4>Register for Activation</h4>
        </div>
        {/* <div className="col-6">
          <p>
            Smart IPTV Activation Smart TVs and supported devices can be
            activated after a one-time fee of 5.49 EUR for each TV/device. To
            avoid activation delays, there is no need to wait until the trial
            version expires if everything is working fine for you. Use the panel
            below to activate your TV/device instantly, in automatic mode. Very
            important! Please DO NOT pay if you don't have any channels to use
            with the application (no channels are provided with activation
            payment) or if something is not working for you, because the payment
            won't solve any of these problems. And you will not be refunded if
            you do. NO CHANNELS are provided with activation, you have to add
            your own. No information will be provided, where you can get a
            working playlist for your needs. Please don't ask about channels
            even if you activate your device.
          </p>
        </div> */}
        <div className="col-6 offset-3">
          <div className="row">
            <form id="payment-form" onSubmit={handleSubmit}>
              <div className="row">
                <div className="col-12">
                  <p>
                    (<span className="text-danger">*</span>
                    <i>marked fields are required.</i>)
                  </p>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label htmlFor="email">
                      Recovery Email <span className="text-danger">*</span>
                    </label>
                    <TextFieldGroup
                      type="email"
                      className="form-control"
                      id="email"
                      name="email"
                      placeholder="example@example.com"
                      onChange={handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label htmlFor="mac">
                      MAC address <span className="text-danger">*</span>
                    </label>
                    <TextFieldGroup
                      type="text"
                      className="form-control"
                      id="mac"
                      name="mac"
                      placeholder="00:aa:bb:cc:dd:11"
                      onChange={handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label htmlFor="pin">
                      PIN <span className="text-danger">*</span>
                    </label>
                    <TextFieldGroup
                      type="password"
                      className="form-control"
                      id="pin"
                      name="pin"
                      placeholder="Enter PIN here"
                      onChange={handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label htmlFor="confirmPin">
                      Confirm PIN <span className="text-danger">*</span>
                    </label>
                    <TextFieldGroup
                      type="password"
                      className="form-control"
                      id="confirmPin"
                      name="confirmPin"
                      placeholder="Enter Confirm PIN here"
                      onChange={handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-12">
                  <label htmlFor="payment">
                    Payment <span className="text-danger">*</span>
                  </label>
                  <CardElement
                    id="card-element"
                    options={cardStyle}
                    onChange={handleChange}
                  />
                  {/* Show any error that happens when processing the payment */}
                  {error && (
                    <div className="card-error text-danger" role="alert">
                      {error}{" "}
                      <span onClick={() => window.location.reload()}>
                        Reload
                      </span>
                    </div>
                  )}
                  {/* <div className="my-4">
                    <ReCAPTCHA
                      ref={(r) => (reCaptcha = r)}
                      sitekey="6Ld5wX4fAAAAAKcBjkM3iUtfD98mI36ICAARpjWt"
                      grecaptcha={grecaptchaObject}
                      onChange={onCaptchaChange}
                    />
                  </div> */}

                  <button
                    // disabled={processing || disabled || succeeded}
                    id="submit"
                    className="my-3 btn-dark"
                  >
                    <span id="button-text">
                      <i className="fa fa-dollar-sign"></i>&nbsp;Pay Now
                      {/* {processing ? (
                    <div className="spinner" id="spinner"></div>
                  ) : (
                    "Pay now"
                  )} */}
                    </span>
                  </button>
                </div>
              </div>

              {/* Show a success message upon completion */}
              {/* <p className={succeeded ? "result-message" : "result-message hidden"}>
        Payment succeeded, see the result in your
        <a href={`https://dashboard.stripe.com/test/payments`}>
          
          Stripe dashboard.
        </a>
        Refresh the page to pay again.
      </p> */}
            </form>
          </div>
        </div>
      </div>
      {/* <div className="row text-center">
        <div className="col-12">
          <h2>-OR-</h2>
          <h5>
            <Link to="/reactivation">I am here to reactivate</Link>
          </h5>
        </div>
      </div> */}
    </FrontEndLayout>
  );
};

export default ActivationPage;
