import React, { useState, useEffect } from "react";
import { useHistory, Link } from "react-router-dom";
import SpatialNavigation, {
  Focusable,
  FocusableSection,
} from "react-js-spatial-navigation";
import FrontEndLayout from "../../layouts/FrontEndLayout";
import SecondFrontEndHeader from "../../layouts/SecondFrontEndHeader";
import Image from "../../components/image/Image";
// import { channelList } from "../../assets/ChannelList";
import { useDispatch, useSelector } from "react-redux";
import { getPlayListBasedOnMac } from "../../../actions/macActions";
import defaultChannelImage from "../../assets/images/img1.png";
import placeholderImage from "../../assets/images/placeholder.png";
import "./HomePage.css";
import Loader from "../../components/Loader";
const Homepage = () => {
  const navigate = useHistory();
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    dispatch(getPlayListBasedOnMac());
  }, []);
  const channelList = useSelector((state) => state.mac.channelList);
  console.log("channelList ", channelList);
  // if (channelList) {
  //   setLoading(true);
  // }
  // channelList.items.length > 0 ?
  // setLoading(false) : setLoading(true);
  const [channels, setChannels] = useState(
    channelList ? channelList.items.slice(0) : null
  );
  const dispatch = useDispatch();
  const filterHandler = (val) => {
    const newChannels =
      channelList != undefined
        ? channelList.items.filter((item) => {
            return item.raw.includes(val);
          })
        : null;
    setChannels(newChannels);
  };

  useEffect(() => {
    const backEvent = function (e) {
      if (e.keyName === "back") {
        console.log("back event", e, window.location.href);
        window.history.back();
      }
    };

    // add eventListener for tizenhwkey (Back Button)
    window.addEventListener("tizenhwkey", backEvent);
    return () => {
      window.removeEventListener("tizenhwkey", backEvent);
    };
  }, []);

  return (
    <React.Fragment>
    {loading == false ? 
      <FrontEndLayout className="homepageWrapper">
        {channelList != undefined ? (
          <React.Fragment>
            <SecondFrontEndHeader filterHandler={filterHandler} />
            <SpatialNavigation>
              <section
                className="contentWrapper"
                style={{ background: "black" }}
              >
                <div className="channelListWrapper">
                  <ul className="channelList">
                    <FocusableSection>
                      <Focusable
                        onClickEnter={() => {
                          filterHandler("");
                        }}
                      >
                        <li
                          onClick={() => {
                            filterHandler("");
                          }}
                          className="channelListItem"
                        >
                          All
                        </li>
                      </Focusable>
                      <Focusable
                        onClickEnter={() => {
                          filterHandler("EN");
                        }}
                      >
                        <li
                          onClick={() => {
                            filterHandler("EN");
                          }}
                          className="channelListItem"
                        >
                          English
                        </li>
                      </Focusable>
                      <Focusable
                        onClickEnter={() => {
                          filterHandler("China");
                        }}
                      >
                        <li
                          onClick={() => {
                            filterHandler("China");
                          }}
                          className="channelListItem"
                        >
                          Chinese
                        </li>
                      </Focusable>
                      <Focusable
                        onClickEnter={() => {
                          filterHandler("French");
                        }}
                      >
                        <li
                          onClick={() => {
                            filterHandler("French");
                          }}
                          className="channelListItem"
                        >
                          French
                        </li>
                      </Focusable>
                    </FocusableSection>
                  </ul>
                </div>
                <div className="channelsView">
                  {channels != undefined && channels.length > 0 ? (
                    channels.map((channelItem, index) => {
                      return (
                        <Focusable
                          onClickEnter={() => {
                            navigate(
                              `/channel?channelUrl=${window.encodeURIComponent(
                                channelItem.url
                              )}`
                            );
                          }}
                          key={index}
                        >
                          <Link
                            to={`/channel?channelUrl=${window.encodeURIComponent(
                              channelItem.url
                            )}`}
                          >
                            <span
                              className="channelItem"
                              title={channelItem.url}
                            >
                              <Image
                                url={channelItem.tvg.logo}
                                altUrl={defaultChannelImage}
                                alt="channelImage"
                                placeholderImage={placeholderImage}
                              />
                            </span>
                          </Link>
                        </Focusable>
                      );
                    })
                  ) : (
                    <div className="noResults">Sorry, No Results Found!</div>
                  )}
                </div>
              </section>
            </SpatialNavigation>
          </React.Fragment>
        ) : (
          <div className="text-center my-5">
            <h4>
              Not Subscribed to any channel <Link to="/form">Add Playlist</Link>
            </h4>
            <h5>
              New Here? <Link to="/activation">Activate</Link>
              <br></br>
            </h5>
          </div>
        )}
      </FrontEndLayout>
      : <Loader/>}
    </React.Fragment>
  );
};

export default Homepage;
