import axios from "axios";
import { toast } from "react-toastify";

import { SAVE_FAQ, GET_ERRORS, GET_ALL_FAQS, DELETE_A_FAQ } from "./types";
import { API_URL } from "../common/Constant";
// Save Mac Form
export const saveFaq = (payload) => (dispatch) => {
  console.log("asd123 123 123 12 3 &&&&&&&&&&&", payload);
  let data = {
    title: payload.title,
    body: payload.body,
    // file: payload.file,
  };
  axios
    .post(API_URL + "/faq/saveFaq", payload)
    .then((result) => {
      console.log("asd123 123 123 12 3 result", result);
      dispatch(saveFaqAction(result));
      toast.success(result.data.message, { autoClose: 2300 });
    })
    .catch((err) =>
      // dispatch({ type: GET_ERRORS, payload: err.response.data })
      dispatch({ type: GET_ERRORS, payload: err.response })
    );
};
// Save Mac Form
export const getAllFaqs = () => (dispatch) => {
  axios
    .post(API_URL + "/faq/getAllFaqs")
    .then((result) => {
      console.log("getAllFaqsFormAction", result);
      dispatch(getAllFaqsFormAction(result));
      toast.success(result.data.message, { autoClose: 2300 });
    })
    .catch((err) =>
      // dispatch({ type: GET_ERRORS, payload: err.response.data })
      dispatch({ type: GET_ERRORS, payload: err.response })
    );
};
// Save Mac Form
export const deleteAFaq = (id) => (dispatch) => {
  const data =  {
    id: id
  }
  axios
    .post(API_URL + "/faq/deleteAFaq", data)
    .then((result) => {
      console.log("deleteAFaqAction", result);
      dispatch(deleteAFaqAction(result));
      toast.success(result.data.message, { autoClose: 2300 });
    })
    .catch((err) =>
      // dispatch({ type: GET_ERRORS, payload: err.response.data })
      dispatch({ type: GET_ERRORS, payload: err.response })
    );
};
// SAve MAC form
export const deleteAFaqAction = (res) => {
  return { type: DELETE_A_FAQ, payload: res };
};
// SAve MAC form
export const saveFaqAction = (res) => {
  return { type: SAVE_FAQ, payload: res };
};
// SAve MAC form
export const getAllFaqsFormAction = (res) => {
  return { type: GET_ALL_FAQS, payload: res };
};
