import React from 'react';
import AdminRoute from "../common/AdminRoute";
import BackEndDashboard from "../backend/pages/BackEndDashboard"; 
import BackEndUserCrud from "../backend/pages/admin/user/BackEndUserCrud"; 
import BackEndMacCrud from "../backend/pages/admin/mac/BackEndMacCrud"; 
import BackEndFaqCrud from "../backend/pages/admin/faq/BackEndFaqCrud"; 
let array = [
  <AdminRoute exact path="/admin/dashboard" component={BackEndDashboard} />,

  <AdminRoute exact path="/admin/user" component={BackEndUserCrud} />,

  <AdminRoute exact path="/admin/mac" component={BackEndMacCrud} />,

  <AdminRoute exact path="/admin/faq" component={BackEndFaqCrud} />,
];

export default array;
