import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
// import {Link} from 'react-router-dom';
import BackEndLayout from "../../../layouts/BackEndLayout";
import { getAllMacs } from "../../../../actions/macActions"; 
import MaterialTable from "material-table";
import { Link } from "@material-ui/core";

const BackEndUserCrud = (props) => { 
  const user = useSelector((state) => state.user.user);
  const initialUserState = {
    id: user && user.id,
    firstName: user && user.firstName,
    lastName: user && user.lastName,
  }; 
  const macs = useSelector((state) => state.mac.macs); 
  const dispatch = useDispatch(); 
  useEffect(() => {
    dispatch(getAllMacs()); 
  }, []);
 

  const columns = [
    { title: "MAC address", field: "mac" },
    { title: "EPG country", field: "epgCountry" },
    { title: "Created At", field: "createdAt" },
    { title: "Deleted At", field: "deletedAt" },
  ];
  const options = {
    search: true,
    paging: true,
  };
  const tableTitle = "MAC Addresses' List";

  return (
    <BackEndLayout>
      <div className="content-wrapper">
        <div className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6"> 
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item">
                    <Link to="/">Home</Link>
                  </li>
                  <li className="breadcrumb-item active">MAC Addresses</li>
                </ol>
              </div>
            </div>
          </div>
        </div>
        <section className="content">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <div className="card">
                  <div className="card-body">
                    {macs && macs.length > 0 && ( 
                      <MaterialTable
                        title={tableTitle}
                        data={macs}
                        columns={columns}
                        options={options}
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </BackEndLayout>
  );
};

export default BackEndUserCrud;
