import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import TextFieldGroup from "../../../common/TextFieldGroup";
import FrontEndLayout from "../../layouts/FrontEndLayout";
import isEmpty from "../../../validations/is-empty";
import checkEmail from "../../../validations/checkEmail";
import { toast } from "react-toastify";
import {
  sendRecoveryEmail,
  submitRecoveryOtp,
  submitNewPin,
} from "../../../actions/activationActions";

function RecoverPage() {
  const dispatch = useDispatch();
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [otp, setOtp] = useState("");
  const [pin, setPin] = useState("");
  const [confirmPin, setConfirmPin] = useState("");
  const [emailScreen, setEmailScreen] = useState(true);
  const [otpScreen, setOtpScreen] = useState(false);
  const [resetPINScreen, setResetPINScreen] = useState(false);
  const { recoveryEmailSent, OtpEnterDone, enterNewPinDone } = useSelector(
    (state) => state.activation
  );

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    if (name === "email") {
      return setEmail(value);
    }
    if (name === "otp") {
      return setOtp(value);
    }
    if (name === "pin") {
      return setPin(value);
    }
    if (name === "confirmPin") {
      return setConfirmPin(value);
    }
  };

  const sendRecoveryEmailFunc = (e) => {
    if (isEmpty(email)) {
      toast.error("Email is required!");
      return;
    }
    dispatch(sendRecoveryEmail(email, history));
    console.log("recoveryEmailSent ", recoveryEmailSent);
  };

  useEffect(() => {
    if (recoveryEmailSent == true) {
      setEmailScreen(false);
      setOtpScreen(true);
      setResetPINScreen(false);
    }
  }, [recoveryEmailSent]);

  useEffect(() => {
    if (OtpEnterDone == true) {
      setEmailScreen(false);
      setOtpScreen(false);
      setResetPINScreen(true);
    }
  }, [OtpEnterDone]);

  useEffect(() => {
    if (enterNewPinDone == true) {
      setEmailScreen(false);
      setOtpScreen(false);
      setResetPINScreen(false);
    }
  }, [enterNewPinDone]);

  useEffect(() => {
    console.log("kkkk");
    setEmailScreen(true);
    setOtpScreen(false);
    setResetPINScreen(false);
  }, []);

  const submitRecoveryOtpFunc = (e) => {
    if (isEmpty(otp)) {
      toast.error("OTP is required!");
      return;
    }
    dispatch(submitRecoveryOtp({ otp, email }, history));
  };

  const submitNewPinFunc = (e) => {
    if (isEmpty(pin)) {
      toast.error("PIN is required!");
      return;
    }
    if (isEmpty(confirmPin)) {
      toast.error("Confirm PIN is required!");
      return;
    }
    if (pin != confirmPin) {
      toast.error("PIN and confirm PIN does not match");
      return;
    }
    dispatch(submitNewPin({ email, pin }, history));
  };

  return (
    <div>
      <FrontEndLayout>
        <div className="container">
          {emailScreen ? (
            <div className="row p-3 ">
              <div className="col-12 text-center my-5 ">
                <h4>Recover your PIN.</h4>
              </div>
              <div className="col-6 offset-3">
                <div className="row">
                  <div className="col-12">
                    <div className="form-group">
                      <label htmlFor="email">
                        Enter your Recovery Email
                        <span className="text-danger">*</span>
                      </label>
                      <TextFieldGroup
                        type="email"
                        className="form-control"
                        id="email"
                        name="email"
                        placeholder=""
                        onChange={handleInputChange}
                      />
                    </div>
                  </div>
                  <div className="col-12">
                    <button
                      className="btn btn-md btn-success mt-3"
                      onClick={() => sendRecoveryEmailFunc()}
                    >
                      Send Email&nbsp;<i className="fa fa-envelope"></i>
                    </button>
                  </div>
                </div>
              </div>

              <div className="col-4"></div>
            </div>
          ) : (
            ""
          )}

          {otpScreen ? (
            <div className="row p-3 ">
              <div className="col-12 text-center my-5 ">
                <h4>Enter your OTP.</h4>
              </div>
              <div className="col-6 offset-3">
                <div className="row">
                  <div className="col-12">
                    <div className="form-group">
                      <label htmlFor="otp">
                        Enter your Recovery OTP
                        <span className="text-danger">*</span>
                      </label>
                      <TextFieldGroup
                        type="otp"
                        className="form-control"
                        id="otp"
                        name="otp"
                        placeholder=""
                        onChange={handleInputChange}
                      />
                    </div>
                  </div>
                  <div className="col-3">
                    <button
                      className="btn btn-md btn-dark mt-3"
                      onClick={submitRecoveryOtpFunc}
                    >
                      <i className="fa fa-check"></i>&nbsp;Submit OTP
                    </button>
                  </div>
                  <div className="col-3">
                    <button
                      className="btn btn-md btn-success mt-3"
                      onClick={sendRecoveryEmailFunc}
                    >
                      <i className="fa fa-recycle"></i>&nbsp;Resend OTP
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ) : (
            ""
          )}

          {resetPINScreen ? (
            <div className="row p-3 ">
              <div className="col-12 text-center my-5 ">
                <h4>Enter your PIN and confirm PIN.</h4>
              </div>
              <div className="col-6 offset-3">
                <div className="row">
                  <div className="col-12">
                    <div className="form-group">
                      <label htmlFor="pin">
                        Enter your new PIN
                        <span className="text-danger">*</span>
                      </label>
                      <TextFieldGroup
                        type="pin"
                        className="form-control"
                        id="pin"
                        name="pin"
                        placeholder=""
                        onChange={handleInputChange}
                      />
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="form-group">
                      <label htmlFor="confirmPin">
                        Enter your confirm PIN
                        <span className="text-danger">*</span>
                      </label>
                      <TextFieldGroup
                        type="confirmPin"
                        className="form-control"
                        id="confirmPin"
                        name="confirmPin"
                        placeholder=""
                        onChange={handleInputChange}
                      />
                    </div>
                  </div>
                  <div className="col-2">
                    <button
                      className="btn btn-sm btn-success mt-3"
                      onClick={submitNewPinFunc}
                    >
                      <i className="fa fa-save"></i>&nbsp;Submit
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ) : (
            ""
          )}
        </div>
      </FrontEndLayout>
    </div>
  );
}

export default RecoverPage;
