import { SAVE_FAQ, GET_ALL_FAQS, DELETE_A_FAQ } from "../actions/types";
// import isEmpty from "../../src/validations/is-empty";

const initialState = {
  title: "",
  body: "",
};
 
export default function (state = initialState, action) {
  console.log("object: action", action);
  switch (action.type) {
    case SAVE_FAQ:
      return {
        ...state,
        body: action.payload.data.body,
        title: action.payload.data.title,
      };
    case GET_ALL_FAQS:
      return {
        ...state,
        faqs: action.payload.data.faqs, 
      };
    case DELETE_A_FAQ:
      return {
        ...state,
        faqs: action.payload.data.faqs, 
      };
    default:
      return state;
  }
}
