import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import BackEndLayout from "../layouts/BackEndLayout";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { withRouter } from "react-router";
import { getAllMacs } from "../../actions/macActions";

const BackEndDashboard = (props) => {
  const dispatch = useDispatch(); 
  useEffect(() => {
    if (props.auth.isAuthenticated === false) {
      props.history.push("/login");
    }
    dispatch(getAllMacs());
  }, []);
  
  const macCount = useSelector((state) => state.mac.macs);

  return (
    <BackEndLayout>
      <div className="content-wrapper">
        <div className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1 className="m-0 text-dark">Admin Dashboard</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item">
                    <Link to="/">Home</Link>
                  </li>
                  <li className="breadcrumb-item active">Dashboard</li>
                </ol>
              </div>
            </div>
          </div>
        </div>
        <section className="content">
          <div className="container-fluid">
            <div className="row">
              <div className="col-lg-4 col-6">
                <div className="small-box bg-success">
                  <div className="inner">
                    <h3>{macCount.length}</h3>
                    <h4>MAC ADDRESSES</h4>
                  </div>
                  <div className="icon">
                    {/* <i className="ion ion-bag" /> */}
                  </div>
                  <Link to="/admin/mac" className="small-box-footer">
                    More info <i className="fas fa-arrow-circle-right" />
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </BackEndLayout>
  );
};
BackEndDashboard.propTypes = {
  auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({ auth: state.auth });

export default withRouter(connect(mapStateToProps, {})(BackEndDashboard));
